import React from 'react'
import styled from 'react-emotion'
import Loader from 'halogen/PulseLoader'

const Loading = styled('div')`
  margin-top: 100px;
  text-align: center;
  font-size: 30px;
`

const Small = styled('div')`
  margin-top: 2px;
  font-size: 11px;
  color: deeppink;
`

export const Licences = ({ isLoading, error, data }) => {
  if (!isLoading) {
    return <Loading>Loading, this may take a while...</Loading>
  }

  return <div>{JSON.stringify(data)}</div>
}



